import React, { useState, useRef, useCallback, useEffect } from "react";
import { Grid } from "@material-ui/core";
import { ErrorBoundary } from "react-error-boundary";
import { useSelector } from "react-redux";
import { useQuery, UseQueryResult } from "react-query";
import ApiErrorPage from "../../../Errror/ApiErrorPage";

import {
  SingleCategoryBySdgsTitle,
  RootAppState,
  SingleGoalObject,
} from "../../../../utils/typeHelpers";
import Loader from "../../../../components/Loader";
import ErrorCatchComponent from "../../../../components/ErrorCatchComponent";

import PageErrorBoundary from "../../../Errror/PageErrorBoundary";
import PostCard from "./PostCard";
import useAxios from "../../../../utils/useAxios";
import { API_URL } from "../../../../utils/Api";

const SingleCategory = ({ id }: { id: number }) => {
  const userState = useSelector((state: RootAppState) => state.AuthReducer);
  const observer = useRef<IntersectionObserver>();
  const [pageno, setPageno] = useState("1");
  const [refetch, setRefetch] = useState(false);
  const axiosInstance = useAxios();

  const {
    data,
    isError,
    error,
  }: UseQueryResult<SingleCategoryBySdgsTitle, Error> = useQuery(
    [
      "singleCategory",
      id,
      pageno,
      userState.isLoggedIn ? userState.userInfo.access : "",
      refetch,
    ],
    async () => {
      const response = await axiosInstance.get(
        `${API_URL}/single-catagory/${id}?page=${pageno}`
      );
      return response.data;
    }
  );

  const [explode, setExplode] = useState(false);

  const [goals, setGoals] = useState<SingleGoalObject[] | []>([]);

  useEffect(() => {
    let cancel = false;

    if (goals.length > 0 && data?.results !== undefined && pageno !== "1") {
      setGoals((oldGoals) => [...oldGoals, ...data?.results]);
      setPageno("1");
    } else if (data?.results !== undefined) {
      setGoals(data?.results);
    }
    return () => {
      cancel = true;
    };
  }, [data]);

  const singleCategoryElementRef = useCallback(
    (node) => {
      if (observer.current) observer.current?.disconnect();
      observer.current = new IntersectionObserver((entries) => {
        if (entries[0].isIntersecting && data?.next !== null) {
          var str = data?.next;
          var res: RegExpMatchArray | null | undefined = str?.match(
            new RegExp("page=([^/]+)")
          );
          if (res) {
            const subUri: string = res[1];
            setPageno(subUri);
          }
        }
      });
      if (node) observer.current.observe(node);
    },
    [pageno, data]
  );
  const handleRefetch = () => setRefetch(!refetch);

  return (
    <ErrorCatchComponent>
      {isError ? (
        <ApiErrorPage
          error={error ? error : new Error("Network response was not ok.")}
        />
      ) : (
        <ErrorBoundary
          fallbackRender={({ error, resetErrorBoundary }) => (
            <PageErrorBoundary
              error={error}
              resetErrorBoundary={resetErrorBoundary}
            />
          )}
          onReset={() => setExplode(false)}
          resetKeys={[explode]}
        >
          {goals === [] ? (
            <Loader />
          ) : (
            <Grid
              container
              direction="row"
              justifyContent="space-evenly"
              alignItems="center"
            >
              {goals.map((goalItem, index) => {
                if (goals.length === index + 1) {
                  return (
                    <div ref={singleCategoryElementRef}>
                      <PostCard
                        keyy={`${index}`}
                        goalItem={goalItem}
                        handleRefetch={handleRefetch}
                      />
                    </div>
                  );
                } else {
                  return (
                    <div style={{ cursor: "pointer" }}>
                      <PostCard
                        keyy={`${index}`}
                        goalItem={goalItem}
                        handleRefetch={handleRefetch}
                      />
                    </div>
                  );
                }
              })}
            </Grid>
          )}
        </ErrorBoundary>
      )}
    </ErrorCatchComponent>
  );
};

export default SingleCategory;
