import React from "react";
import { useLocation } from "react-router";
import { Grid, Box, Typography, Container } from "@material-ui/core";

import SingleCategory from "./SingleCategory";
import { SdgsType } from "../../../../utils/typeHelpers";

import Alert from "../../Common/Alert";
import "./styles.css";

interface StateRouteProps {
  item?: SdgsType | null;
}

const SingleCategoryPage = () => {
  const location = useLocation();

  const idItem: StateRouteProps =
    location.state !== null ? location.state : { item: null };

  return (
    <Container>
      {idItem.item?.id !== null && idItem.item?.id !== undefined ? (
        <div>
          <div className="sdgs-category-div">
            <Box
              display="flex"
              justifyContent="center"
              alignItems="center"
              sx={{ paddingTop: 2, paddingBottom: 3 }}
            >
              <img
                src={idItem.item.thumbnail != null ? idItem.item.thumbnail : ""}
              />
              <Typography variant="h6">{idItem.item.title}</Typography>
            </Box>
          </div>

          <SingleCategory id={idItem.item?.id} />
        </div>
      ) : (
        <Alert
          title="Error!"
          desc="Sorry, we couldn’t find any content of this page."
        />
      )}
    </Container>
  );
};

export default SingleCategoryPage;
