import React, { useRef, useState, useEffect } from "react";

import Card from "@material-ui/core/Card";
import ReactPlayer from "react-player";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import { Icon } from "@iconify/react";

import FavoriteIconOutline from "../../../../assets/icons/heart.svg";
import FavoriteIconfill from "../../../../assets/icons/heartfilled.svg";
import SavedIconOutline from "../../../../assets/icons/pin_outline.svg";
import SavedIconFill from "../../../../assets/icons/pin.svg";
import DonateIcon from "../../../../assets/icons/hand.svg";

import FollowButton from "../../../../assets/icons/follow.png";
import FollowedButton from "../../../../assets/icons/followed.png";

import { Button, CardMedia, Grid, Stack, Typography } from "@material-ui/core";
import { useNavigate } from "react-router";
import { styled } from "@material-ui/styles";
import commentIcon from "@iconify/icons-ant-design/comment-outlined";

import { useSelector } from "react-redux";
import {
  APP_URL,
  FOLLOW_UNFOLLOW_API,
  GOAL_LIKE_UNLIKE_API,
  GOAL_SAVE_UNSAVE_API,
} from "../../../../utils/Api";
import { MessageError } from "../../../../components/AlertMessageError";
import { customShadows } from "../../../../theme/shadows";
import { useWindowDimensions } from "../../../../utils/helperFunction";
import ShareMenu from "../../../../components/ShareMenu";
import {
  AddFunction,
  GoalHomeType,
  RootAppState,
  SingleGoalObject,
} from "../../../../utils/typeHelpers";

const CardMediaStyled = styled(CardMedia)(() => ({
  marginLeft: 60,
  marginBottom: 10,
  marginTop: 10,
  borderRadius: 7,
  cursor: "pointer",
  justifyContent: "center",
  alignItems: "center",
  display: "flex",
}));

const UsernameText = styled(Typography)(() => ({
  fontSize: 17,
  textTransform: "capitalize",
  color: "#007AFF",
  wordBreak: "break-word",
  width: 150,
  cursor: "pointer",
  fontWeight: 600,
}));

const EndorserText = styled(Typography)(() => ({
  color: "#6C757D",
  fontSize: 13,
  fontWeight: 400,
  wordBreak: "break-word",
  width: 150,
}));

const EndorserSpan = styled("span")(() => ({
  color: "#007AFF",
  fontSize: 13,
  fontWeight: 400,
  textTransform: "capitalize",
  wordBreak: "break-word",
  width: 150,
  cursor: "pointer",
}));

const GridMedia = styled(Grid)(() => ({
  display: "flex",
  flex: 1,
  flexDirection: "row",
  justifyContent: "space-around",
}));

const PostCard: React.FC<{
  goalItem: SingleGoalObject;
  keyy: string;
  handleRefetch: any;
}> = ({ goalItem, keyy, handleRefetch }) => {
  const { width } = useWindowDimensions();
  const userState = useSelector((state: RootAppState) => state.AuthReducer);
  const [playing, setPlaying] = useState(false);
  const videoRef = useRef<HTMLHeadingElement>(null);
  const refError = useRef<null | AddFunction>(null);

  let navigate = useNavigate();

  const viewGoalDetails = () => navigate(`../viewgoal/${goalItem.slug}`);

  const makeDonation = () =>
    navigate("../donate", { state: { slug: goalItem.slug } });

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleRefetchUnlike = async (goalID: number) => {
    if (userState.isLoggedIn && goalID) {
      GOAL_LIKE_UNLIKE_API(goalID, userState.userInfo.access).then((res) => {
        if (res.is_like === true) {
          handleRefetch();
        } else if (res.is_like === false) {
          handleRefetch();
        } else {
          handleCreateError("Something went wrong");
        }
      });
    } else {
      navigate("../login");
    }
  };
  const handleSaveUnSave = async (goalID: number) => {
    if (userState.isLoggedIn && goalID) {
      GOAL_SAVE_UNSAVE_API(goalID, userState.userInfo.access).then((res) => {
        if (res.is_saved === true) {
          handleRefetch();
        } else if (res.is_saved === false) {
          handleRefetch();
        } else {
          handleCreateError("Something went wrong");
        }
      });
    } else {
      navigate("../login");
    }
  };

  const handleFollowUnfollow = async () => {
    if (userState.isLoggedIn && goalItem.profile_id) {
      FOLLOW_UNFOLLOW_API(
        "profile-follow",
        goalItem.profile_id,
        userState.userInfo.access
      ).then((res) => {
        if (res.is_followed === true) {
          handleRefetch();
        } else if (res.is_followed === false) {
          handleRefetch();
        } else {
          handleCreateError("Something went wrong");
        }
      });
    } else {
      navigate("../login");
    }
  };

  useEffect(() => {
    let options = {
      rootMargin: "0px",
      threshold: [0.25, 0.75],
    };

    let handlePlay = (entries: any) => {
      entries.forEach((entry: any) => {
        if (entry.isIntersecting) {
          setPlaying(true);
        } else {
          setPlaying(false);
        }
      });
    };

    let observer = new IntersectionObserver(handlePlay, options);

    if (videoRef.current) {
      observer.observe(videoRef.current);
    }
  });
  const handleSeeOtherProfile = () => {
    navigate("../profiledetailother", {
      state: { goalID: goalItem.profile_id },
    });
  };

  return (
    <div key={keyy} ref={videoRef}>
      <MessageError
        children={(add: AddFunction) => {
          refError.current = add;
        }}
      />

      <Grid
        item
        sm={6}
        xs={12}
        md={4}
        sx={{
          marginBottom: 2,
          minWidth: width < 500 ? 350 : 400,
          borderRadius: 7,
        }}
      >
        <Card sx={{ boxShadow: customShadows.z1 }}>
          <Stack
            direction="row"
            justifyContent="space-between"
            alignItems="center"
            spacing={1}
            sx={{ padding: 1 }}
          >
            <Avatar src={goalItem.profile_image} />
            <Stack
              direction="column"
              justifyContent="flex-start"
              spacing={0}
              sx={{ padding: 1 }}
            >
              <UsernameText onClick={handleSeeOtherProfile}>
                @{goalItem.profile_username}
              </UsernameText>

              <EndorserText gutterBottom>
                Endrosed by{" "}
                <EndorserSpan>@{goalItem.ngo_username}</EndorserSpan>
              </EndorserText>
            </Stack>
            <Button onClick={handleFollowUnfollow} aria-label="FollowButton">
              <img
                src={
                  userState.isLoggedIn
                    ? goalItem.is_followed
                      ? FollowedButton
                      : FollowButton
                    : FollowButton
                }
                style={{ width: 80 }}
              />
            </Button>
          </Stack>

          <GridMedia
            container
            direction="row"
            justifyContent="space-around"
            alignItems="center"
          >
            <Grid item xs={9}>
              <CardMediaStyled
                onClick={viewGoalDetails}
                sx={{ height: width < 500 ? 350 : 450 }}
              >
                <ReactPlayer
                  style={{
                    borderRadius: 7,
                    borderColor:
                      "rgb(146, 96, 204) rgb(71, 175, 228) rgb(46, 128, 177) rgb(87, 88, 198)",
                    borderStyle: "solid",
                  }}
                  height={width < 500 ? 340 : 440}
                  width={width < 500 ? 260 : 300}
                  url={goalItem.goal_media[0].file}
                  loop={true}
                  playing={playing}
                  controls={true}
                  muted={true}
                  config={{
                    file: {
                      attributes: {
                        controlsList: "nodownload noplaybackrate",
                        disablePictureInPicture: "true",
                      },
                    },
                  }}
                />
              </CardMediaStyled>
            </Grid>
            <Grid item xs={3}>
              <Stack
                direction="column"
                justifyContent="flex-end"
                alignItems="center"
                spacing={2}
              >
                <div>
                  <Grid
                    container
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <IconButton aria-label="share" onClick={makeDonation}>
                      <img src={DonateIcon} alt="logo" className="cardIcons" />
                    </IconButton>
                    <p></p>
                  </Grid>
                </div>
                <div>
                  <Grid
                    container
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <IconButton
                      onClick={() => handleRefetchUnlike(goalItem.goal)}
                      aria-label="like"
                    >
                      <img
                        src={
                          userState.isLoggedIn
                            ? goalItem.is_liked
                              ? FavoriteIconfill
                              : FavoriteIconOutline
                            : FavoriteIconOutline
                        }
                        alt="logo"
                        className="cardIcons"
                      />
                    </IconButton>

                    <p>{goalItem.total_like_count}</p>
                  </Grid>
                </div>
                <div>
                  <Grid
                    container
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <IconButton onClick={viewGoalDetails} aria-label="share">
                      <Icon
                        icon={commentIcon}
                        width={22}
                        height={22}
                        color="#007AFF"
                      />
                    </IconButton>

                    <p>{goalItem.total_comment_count}</p>
                  </Grid>
                </div>
                <div>
                  <Grid
                    container
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <IconButton
                      onClick={() => handleSaveUnSave(goalItem.goal)}
                      aria-label="add to save"
                    >
                      <img
                        src={
                          userState.isLoggedIn
                            ? goalItem.is_saved
                              ? SavedIconFill
                              : SavedIconOutline
                            : SavedIconOutline
                        }
                        style={{ height: 30, width: 30 }}
                        alt="logo"
                        className="cardIcons"
                      />
                    </IconButton>
                    <p></p>
                  </Grid>
                </div>
                <div>
                  <Grid
                    container
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <ShareMenu
                      url={`${APP_URL}/viewgoal/${goalItem.slug}`}
                      image={goalItem.image}
                    />
                    <p></p>
                  </Grid>
                </div>
              </Stack>
            </Grid>
          </GridMedia>
        </Card>
      </Grid>
    </div>
  );
};

export default PostCard;
